@import "../../css-config/mixins.scss";
.cart-panel{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    z-index: 99;
    .opac-layer{
        background-color: rgba(0, 0, 0, 0.5);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    .cart-content{
        width: 100%;
        height: 100%;
        background: white;
        position: relative;
        z-index: 1;
        flex-direction: column;
        transform: translateX(100%);
        animation: slideCartWindow 0.3s ease forwards;
        @include md{
            width: 340px;
        }
    }
}

@keyframes slideCartWindow {
    0%{
        transform: translateX(100%);
    }

    100%{
        transform: translateX(0);
    }
}