@import "../../../css-config/mixins.scss";

.newsletter-section {
    width: 100%;
    height: 400px;
    display: flex;
    align-items: center;
    background: #f9f9f9 url("../../../assets/newsletter-bg.jpeg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    .newsletter-content {
        width: fit-content;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 auto;
        text-align: center;
    }

    .small-text {
        margin-bottom: 15px;
        text-transform: uppercase;
        color: rgba(0, 0, 0, 0.5);        
    }

    .big-text {
        margin-bottom: 20px;
        font-size: 22px;
        line-height: 30px;
        font-weight: 500;
        text-transform: uppercase;
        padding: 0 30px;
        @include md {
            font-size: 28px;
        }        
    }

    .form {
        display: flex;
        gap: 5px;
        margin-bottom: 10px;

        input {
            width: 200px;
            height: 40px;
            border-radius: 0;
            border: 1px solid rgba(0, 0, 0, 0.2);
            padding: 0 12px;
            font-size: 16px;
            outline: none;
            @include md {
                width: 300px;
            }            
        }

        button {
            outline: 0;
            border: 0;
            height: 40px;
            width: 100px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            transition: all ease 0.3s;
            font-size: 16px;
            color: white;
            background: #8e2de2;
            border-bottom: 3px solid #6516aa;
            @include md {
                width: 120px;
            }
        }

        button:hover {
            background: rgba(142, 45, 226, 0.8);
        }
    }

    .text {
        margin-bottom: 20px;
        font-size: 14px;
        color: rgba(0, 0, 0, 0.5);
    }
    
    .social-icons {
        display: flex;
        gap: 10px;
        .icon{
            width: 30px;
            height: 30px;
            background: rgba(0, 0, 0, 0.8);
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            transition: all ease 0.3s;
            cursor: pointer;
        }

        .icon:hover{
            background: #8e2de2;
        }
    }
}
